        "问题受理人",
        "优先级",
        "流程状态",
        "问题详情",
        "优问题补充",
        "处理说明"
      ],
      formdatavalue: [
        "问题编号XXX",
        "问题名称XXX",
        "荆州市",
        "综合资源",
        "集客",
        "问题上报人XXX",
        "提出人姓名XXX",
        "提出人电话XXX",
        "问题受理人XXX",
        "优先级XXX",
        "流程状态XXX",
        "问题详情XXX",
        "优问题补充XXX",
        "处理说明XXX"
      ],
      formdata: {
        问题编号: "问题编号XXX",
        问题名称: "问题名称XXX",
        归属地市: "荆州市",
        项目: "综合资源",
        专业: "集客",
        问题上报人: "荆州用户",
        提出人姓名: "毛嫚嫚",
        提出人电话: "15826609220",
        问题受理人: "问题受理人XXX",
        优先级: "优先级XXX",
        流程状态: "流程状态XXX",
        问题详情: "问题详情XXX",
        优问题补充: "优问题补充XXX",
        处理说明: "处理说明XXX"
      },
      form: this.$form.createForm(this, { name: "advanced_search" }),
      showformdata: true,
      dataInfor: ""
    };
  },
  computed: {
    count() {
      return this.expand ? 20 : 6;
    }
  },
  updated() {
    // this.form.validateFields((error, values) => {
    //   this.dataInfor = values;
    // });

    console.log("数据发生了改变");
  },
  mounted() {
    console.log(this.formdatakey);
    this.setFromValue();
  },
  methods: {
    getdata() {
      this.form.validateFields((error, values) => {
        this.dataInfor = values;
      });
    },
    show() {
      this.showformdata = !this.showformdata;
    },
    setFromValue() {
      console.log("初始化");
      this.form.setFieldsValue({});
    },
    handleSearch(e) {
      e.preventDefault();
      this.form.validateFields((error, values) => {
        console.log("error", error);
        console.log("Received values of form: ", values);
        this.showformdata = false;
      });
    },

    handleReset() {
      this.form.resetFields();
    },

    toggle() {
      this.expand = !this.expand;
    }
  }
};
</script>
