<template>
  <div class="hello">
    <h1>发送给</h1>
    <p>{{who1}}</p>
    <h1>抄送给</h1>
    <p>{{who2}}</p>
    <h1>主题 {{weekth}}/{{weekall}}--{{mydate}}--星期{{weekday}}</h1>
    <p>【请查收】工作周报-袁国亮-{{mydate}}</p>
    <h1>正文</h1>
    <p>各位领导好！</p>
    <p style="text-indent:2em">下面是{{mydate}}的工作周报，请审阅，谢谢！</p>
  </div>
</template>

<script>
var moment = require("moment");

export default {
  name: "HelloWorld",
  data() {
    return {
      who1:
        "汤欢<tanghuan@hxct.com>;   夏磊<xialei@hxct.com>;   周小诗<zhouxiaoshi@hxct.com>",
      who2: "谢光炜<0310008251@hxct.com>;曾易<0310008166@hxct.com>",
      mydate: "",
      weekday: "",
      weekth: "",
      weekall: ""
    };
  },
  props: {
    msg: String
  },
  mounted() {
    this.getDate();
  },
  methods: {
    getDate() {
      var mydate1 = new Date();

      // console.log("mydate1::: ", mydate1);
      // 计算当前日期
      this.mydate = this.formatDate(mydate1, "-");
      // 计算当前周数
      var vv = new moment();
      console.log("vv::: ", vv);
      var vNowDate = moment(new moment(vv).format("YYYY-MM-DD"));
      var vWeekOfDay = moment(vNowDate).format("W");
      this.weekth = vWeekOfDay;
      // 计算这一年共有几周
      this.weekall = vv.weeksInYear();
      this.weekday = vv.weekday();
      var myDate = new Date();
      this.weekth = getYearWeek(
        myDate.getFullYear(),
        myDate.getMonth() + 1,
        myDate.getDate()
      );
      console.log(
        "getYearWeek()::: ",
        getYearWeek(
          myDate.getFullYear(),
          myDate.getMonth() + 1,
          myDate.getDate()
        )
      );
      return mydate1;
    },
    formatDate(dateStr, type) {
      if (dateStr == undefined || dateStr == "") {
        return "";
      }
      var date = new Date(dateStr);
      var year = "" + date.getFullYear() + "";
      var month =
        "" + (date.getMonth() + 1) < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1 + "";
      var day =
        "" + (date.getDate() < 10 ? "0" + date.getDate() : date.getDate()) + "";
      var hour =
        "" +
        (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) +
        "";
      var minute =
        "" +
        (date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes()) +
        "";
      var second =
        "" +
        (date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds()) +
        "";
      var returnDate;
      if (type == "-") {
        //2019-05-01 00:00:00
        returnDate = year + "-" + month + "-" + day;
      } else if (type == "/") {
        //2019/05/01 00:00:00
        returnDate =
          year +
          "/" +
          month +
          "/" +
          day +
          " " +
          hour +
          ":" +
          minute +
          ":" +
          second;
      }
      return returnDate;
    }
  }
};
function getYearWeek(year, month, day) {
  console.log("year::: ", year);
  console.log("month::: ", month);
  console.log("day::: ", day);
  var date1 = new Date(year, parseInt(month) - 1, day),
    date2 = new Date(year, 0, 1),
    d = Math.round((date1.valueOf() - date2.valueOf()) / 86400000);
  return Math.ceil((d + (date2.getDay() + 1 - 1)) / 7);
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  /* margin: 20px 0 0; */
  font: 2em Microsoft YaHei UI;
}
p {
  line-height: 0px;
  font-family: Microsoft YaHei UI;
  /* font: ; */
}
</style>
