<template>
  <div class="hello">
    <h1>发送给</h1>
    <p>{{who1}}</p>
    <h1>抄送给</h1>
    <p>{{who2}}</p>
    <h1>主题</h1>
    <p>工作日报-袁国亮-{{mydate}}</p>
    <h1>正文</h1>
    <p>各位领导好！</p>
    <p style="text-indent:2em">下面是{{mydate}}的工作日报，请审阅，谢谢！</p>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      who1: "汤欢<tanghuan@hxct.com>;周小诗<zhouxiaoshi@hxct.com>",
      who2: "谢光炜<0310008251@hxct.com>;曾易<0310008166@hxct.com>",
      mydate: ""
    };
  },
  props: {
    msg: String
  },
  mounted() {
    this.getDate();
  },
  methods: {
    getDate() {
      var mydate1 = new Date();

      this.mydate = this.formatDate(mydate1, "-");
      return mydate1;
    },
    formatDate(dateStr, type) {
      if (dateStr == undefined || dateStr == "") {
        return "";
      }
      var date = new Date(dateStr);
      var year = "" + date.getFullYear() + "";
      var month =
        "" + (date.getMonth() + 1) < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1 + "";
      var day =
        "" + (date.getDate() < 10 ? "0" + date.getDate() : date.getDate()) + "";
      var hour =
        "" +
        (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) +
        "";
      var minute =
        "" +
        (date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes()) +
        "";
      var second =
        "" +
        (date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds()) +
        "";
      var returnDate;
      if (type == "-") {
        //2019-05-01 00:00:00
        returnDate = year + "-" + month + "-" + day;
      } else if (type == "/") {
        //2019/05/01 00:00:00
        returnDate =
          year +
          "/" +
          month +
          "/" +
          day +
          " " +
          hour +
          ":" +
          minute +
          ":" +
          second;
      }
      return returnDate;
    }
  }
};
</script>

